import React from 'react'
import logo from './icon.svg'
import {
  Navbar,
  NavbarBrand,
  Nav,
  NavLink,
  NavItem,
  DropdownItem,
  DropdownMenu,
  NavbarToggler,
  Collapse,
  UncontrolledDropdown,
  DropdownToggle,
  NavbarText,
  Spinner,
  Button,
} from 'reactstrap'
import Dposter from './poster.jpg'
class Loader extends React.Component {
  render() {
    return (
      <div className="loader">
        <Spinner>Loading...</Spinner>
      </div>
    )
  }
}
export default class App extends React.Component {
  state = {
    datafile: '',
    checker: false,
    namemovie: 'panda',
    checker2: false,
    type: 'all',
    page: 1,
  }
  search = () => {
    let namem = this.state.namemovie
    this.setState({ checker: false, checker2: false })
    fetch(
      `https://www.omdbapi.com/?apikey=1fdb696c&s=${this.state.namemovie}&page=${this.state.page}`
    )
      .then((res) => {
        return res.json()
      })
      .then((data) => {
        console.log(typeof data.Response)
        if (data.Response == 'True') {
          return this.setState({ datafile: data }, () => {
            this.setState({
              checker: true,
            })
          })
        } else {
          this.setState({ checker2: true })
          {
            this.setState({ datafile: '' })
          }
        }
      })
  }
  getname = (arg) => (e) => {
    this.setState({ [arg]: e.currentTarget.value })
  }
  componentDidMount() {
    this.search()
  }
  old = () => {
    if (this.state.page > 1) {
      this.setState({ page: this.state.page - 1 }, () => {
        this.search()
      })
    }
  }
  add = () => {
    let r = this.state.datafile.totalResults
    r = +r
    if (this.state.page * 9 < r) {
      this.setState({ page: this.state.page + 1 }, () => {
        this.search()
      })
    }
  }
  render() {
    return (
      <>
        <div>
          <div className="mobilenav">
            <h1>Movie app</h1>
            <div className="flexmnav">
              <input
                placeholder="Movie name"
                type="url"
                autoComplete="on"
                name="url"
                onInput={this.getname('namemovie')}
              />
              <img src={logo} alt="" onClick={this.search} />
            </div>
          </div>
          <Navbar color="dark" container dark expand>
            <NavbarBrand href="/">Movie App</NavbarBrand>
            <NavbarToggler />
            <Collapse navbar>
              <Nav className="me-auto" navbar>
                <NavItem>
                  <NavLink href="/components/"></NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="#">About</NavLink>
                </NavItem>
                <UncontrolledDropdown inNavbar nav>
                  <DropdownToggle caret nav>
                    Filter
                  </DropdownToggle>
                  <DropdownMenu>
                    <DropdownItem
                      onClick={() => {
                        {
                          this.setState({ type: 'all' })
                        }
                      }}
                    >
                      All
                    </DropdownItem>
                    <DropdownItem
                      onClick={() => {
                        {
                          this.setState({ type: 'movie' })
                        }
                      }}
                    >
                      Movies
                    </DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem
                      onClick={() => {
                        {
                          this.setState({ type: 'series' })
                        }
                      }}
                    >
                      Series
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </Nav>
              <NavbarText>
                search
                <input
                  placeholder="Movie name"
                  type="url"
                  className="inp"
                  autoComplete="on"
                  name="url"
                  onInput={this.getname('namemovie')}
                />
                <img src={logo} alt="" className="logo" onClick={this.search} />
              </NavbarText>
            </Collapse>
          </Navbar>
        </div>
        <div className="main">
          <div className="select">
            <select name="" id="" onChange={this.getname('type')}>
              <option value="all">Filter</option>
              <option value="all">All</option>
              <option value="movie">Movies</option>
              <option value="series">Series</option>
            </select>
          </div>
          {this.state.checker == true ? (
            this.state.datafile.Search.map((e) => {
              // ***********
              if (this.state.type == 'all') {
                let r = e.Poster
                if (r == 'N/A') {
                  r = Dposter
                  console.log(r)
                }
                return (
                  <div className="box2">
                    <div
                      className="img"
                      style={{
                        backgroundImage: 'url(' + r + ')',
                      }}
                    ></div>
                    <p>{e.Title}</p>
                    <p>{e.Type}</p>
                    <p>{e.Year}</p>
                  </div>
                )
              } else if (this.state.type == 'movie') {
                if (e.Type == 'movie') {
                  let r = e.Poster
                  if (r == 'N/A') {
                    r = Dposter
                    console.log(r)
                  }
                  return (
                    <div className="box2">
                      <div
                        className="img"
                        style={{
                          backgroundImage: 'url(' + r + ')',
                        }}
                      ></div>
                      <p>{e.Title}</p>
                      <p>{e.Type}</p>
                      <p>{e.Year}</p>
                    </div>
                  )
                }
              } else if (this.state.type == 'series') {
                if (e.Type == 'series') {
                  let r = e.Poster
                  if (r == 'N/A') {
                    r = Dposter
                    console.log(r)
                  }
                  return (
                    <div className="box2">
                      <div
                        className="img"
                        style={{
                          backgroundImage: 'url(' + r + ')',
                        }}
                      ></div>
                      <p>{e.Title}</p>
                      <p>{e.Type}</p>
                      <p>{e.Year}</p>
                    </div>
                  )
                }
              }
              // ***********************
            })
          ) : this.state.checker2 == false ? (
            <Loader />
          ) : (
            <h1>Film not found</h1>
          )}
        </div>
        <div className="line"></div>
        <div className="pag">
          <Button onClick={this.old}>Prev</Button>
          <Button onClick={this.add}>Next</Button>
        </div>
      </>
    )
  }
}
